import React, { useEffect, useRef } from 'react'
import { useHistory } from 'react-router-dom'
import { WrapperBox } from './style'
const Cover = () => {
  const circular = useRef(null)
  const start = useRef(null)
  const history = useHistory()
  useEffect(() => {
    let cvs = document.querySelector('#canvas')
    let ctx = cvs.getContext('2d')
    let meteorShower = new MeteorShower(cvs, ctx)
    meteorShower.start()
    let mx, my, timer
    let z = 2
    document.addEventListener('click', function (e) {
      mx = e.pageX
      my = e.pageY
      z = z + 1
      // _wave(mx, my, z)
    })

    // function _wave(i, j, k) {
    //   $('.ui-content').prepend(
    //     '<div class="wave-position water' +
    //       k +
    //       '" style="z-index:' +
    //       k +
    //       ';top:' +
    //       (j - 150) +
    //       'px;left:' +
    //       (i - 150) +
    //       'px;">' +
    //       '<div class="wave-body">' +
    //       '<div class="wave wave5"></div>' +
    //       '<div class="wave wave4"></div>' +
    //       '<div class="wave wave3"></div>' +
    //       '<div class="wave wave2"></div>' +
    //       '<div class="wave wave1"></div>' +
    //       '<div class="wave wave0"></div>' +
    //       '</div>' +
    //       '</div>'
    //   )
    //   setTimeout(function () {
    //     $('.water' + k).remove()
    //   }, 3000)
    // }
    return () => {}
  })
  const handleClick = () => {
    circular.current.style.transform = 'scaleX(1.4) scaleY(1.4)'
    setTimeout(() => {
      history.push('/home')
    }, 1000)
  }
  // 坐标
  class Crood {
    constructor(x = 0, y = 0) {
      this.x = x
      this.y = y
    }
    setCrood(x, y) {
      this.x = x
      this.y = y
    }
    copy() {
      return new Crood(this.x, this.y)
    }
  }
  // 流星
  class ShootingStar {
    constructor(
      init = new Crood(),
      final = new Crood(),
      size = 3,
      speed = 200,
      onDistory = null
    ) {
      this.init = init // 初始位置
      this.final = final // 最终位置
      this.size = size // 大小
      this.speed = speed // 速度：像素/s

      // 飞行总时间
      this.dur =
        (Math.sqrt(
          Math.pow(this.final.x - this.init.x, 2) +
            Math.pow(this.final.y - this.init.y, 2)
        ) *
          1000) /
        this.speed

      this.pass = 0 // 已过去的时间
      this.prev = this.init.copy() // 上一帧位置
      this.now = this.init.copy() // 当前位置
      this.onDistory = onDistory
    }
    draw(ctx, delta) {
      this.pass += delta
      this.pass = Math.min(this.pass, this.dur)

      let percent = this.pass / this.dur

      this.now.setCrood(
        this.init.x + (this.final.x - this.init.x) * percent,
        this.init.y + (this.final.y - this.init.y) * percent
      )

      // canvas
      ctx.strokeStyle = '#fff'
      ctx.lineCap = 'round'
      ctx.lineWidth = this.size
      ctx.beginPath()
      ctx.moveTo(this.now.x, this.now.y)
      ctx.lineTo(this.prev.x, this.prev.y)
      ctx.stroke()

      this.prev.setCrood(this.now.x, this.now.y)
      if (this.pass === this.dur) {
        this.distory()
      }
    }
    distory() {
      this.onDistory && this.onDistory()
    }
  }

  class MeteorShower {
    constructor(cvs, ctx) {
      this.cvs = cvs
      this.ctx = ctx
      this.stars = []

      this.stop = false
      this.playing = false
    }

    createStar() {
      let angle = Math.PI / 3
      let distance = Math.random() * 400
      let init = new Crood(
        (Math.random() * this.cvs.width) | 0,
        (Math.random() * 100) | 0
      )
      let final = new Crood(
        init.x + distance * Math.cos(angle),
        init.y + distance * Math.sin(angle)
      )
      let size = Math.random() * 2
      let speed = Math.random() * 400 + 100
      let star = new ShootingStar(init, final, size, speed, () => {
        this.remove(star)
      })
      return star
    }

    remove(star) {
      this.stars = this.stars.filter((s) => {
        return s !== star
      })
    }

    update(delta) {
      if (!this.stop && this.stars.length < 20) {
        this.stars.push(this.createStar())
      }
      this.stars.forEach((star) => {
        star.draw(this.ctx, delta)
      })
    }

    tick() {
      if (this.playing) return
      this.playing = true

      let now = new Date().getTime()
      let last = now
      let delta

      let _tick = () => {
        if (this.stop && this.stars.length === 0) {
          cancelAnimationFrame(this.T)
          this.playing = false
          return
        }
        delta = now - last
        delta = delta > 500 ? 30 : delta < 16 ? 16 : delta
        last = now
        this.T = requestAnimationFrame(_tick)
        this.ctx.save()
        // ctx.fillStyle = 'rgba(0,0,0,0.2)' // 每一帧用 “半透明” 的背景色清除画布
        this.ctx.fillStyle = 'rgba(0,0,0,0.8)'
        this.ctx.globalCompositeOperation = 'destination-in'
        this.ctx.fillRect(0, 0, this.cvs.width, this.cvs.height)
        this.ctx.restore()
        this.update(delta)
      }
      _tick()
    }

    start() {
      this.stop = false
      this.tick()
    }

    stop() {
      this.stop = true
    }
  }

  return (
    <WrapperBox>
      <canvas id="canvas" width={window.innerWidth} height={window.innerHeight}>
        您的浏览器不支持canvas，请更换浏览器.
      </canvas>
      <div className="title">人们对自己不懂得事，总会觉得有些害怕的。</div>
      <div ref={start} className="start" onClick={() => handleClick()}></div>
      <div ref={circular} className="circular"></div>
      {/* <div class="wave wave5"></div>
      <div class="wave wave4"></div>
      <div class="wave wave3"></div>
      <div class="wave wave2"></div>
      <div class="wave wave1"></div>
      <div class="wave wave0"></div> */}
    </WrapperBox>
  )
}
export default Cover
