import styled from 'styled-components'
export const WrapperBox = styled.div`
  position: relative;
  overflow:hidden;
  width: 100%;
  height: 100%;
  background: #000 url('${require('../../assets/image/bg.jpg')}');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size:100%;
  .title {
    position: absolute;
    top:80%;
    left:52%;
    transform:translateX(-50%);
    font-size:32px;
    color:#fff;
    opacity:0.7;
  }
  .start {
    position: absolute;
    bottom:100px;
    right:100px;
    width:30px;
    height:30px;
    border-radius:50%;
    background-color: #ccc;
    cursor: pointer;
    transition:all 0.5s ease;
  }
  .start:hover {
    background-color: #fff;
    box-shadow:0 0 10px #fff;
  }
  .circular {
    background-color: #fff;
    position:fixed;
    display:block;
    bottom:-1900px;
    right:-1900px;
    border-radius:50%;
    transition:all 1s ease;
    transform: scaleX(0) scaleY(0);
    height:4000px;
    width:4000px;
  }
  .wave{
  position:absolute;
  top:calc((100% - 30px)/2);
  left:calc((100% - 30px)/2);
  width:30px;
  height:30px;
  border-radius:300px;
}
.wave0{
  background:#f00;
  z-index:2;
  background-size:auto 106%;
animation:w 1s forwards;
}
.wave1{
  background:#d00;
  z-index:3;
  animation:w 1s .2s forwards;
}
.wave2{
  background:#b00;
  z-index:4;
  animation:w 1s .4s forwards;
}
.wave3{
  background:#900;
  z-index:5;
  animation:w 1s .5s forwards;
}
.wave4{
  background:#700;
  z-index:6;
  animation:w 1s .8s forwards;
}
.wave5{
  background:#500;
  z-index:7;
  animation:w 1s 1s forwards;
}
@keyframes w{
  0%{
    top:calc((100% - 30px)/2);
    left:calc((100% - 30px)/2);
    width:30px;
    height:30px;
  }
  100%{
    top:calc((100% - 300px)/2);
    left:calc((100% - 300px)/2);
    width:300px;
    height:300px;
  }
}
`
